.weather{padding:0 20px;-webkit-animation:faded .4s;animation:faded .4s}@-webkit-keyframes faded{from{opacity:.1}to{opacity:1}}@keyframes faded{from{opacity:.1}to{opacity:1}}.weather__items{display:flex;align-items:center;justify-content:space-evenly}.weather__icon{width:150px;height:150px}.weather__item{font-size:30px;font-weight:500}.weather__item span{font-size:60px}.weather__list{padding:0 20px;border-top:2px solid rgba(128,128,128,.6);list-style-type:none}.weather__list li{font-size:25px;line-height:38px}.weather__list-line{padding-top:20px;position:relative;width:270px;height:70px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);overflow:hidden}.weather__list-line span{position:absolute;height:270px;width:270px;border:2px solid #000;border-top:2px dotted #000;border-right:2px dotted #000;border-radius:50%}.weather__list-line span:after{content:"";position:absolute;height:20px;width:20px;border:2px solid #ff0;background:#ff0;border-radius:50%;box-shadow:0 0 10px #ff0;top:34px;left:20px}.weather__list-sun{display:flex;justify-content:space-between}.weather__time{margin-top:5px;text-align:center;font-size:25px}@media(max-width: 576px){.weather__time{font-size:20px}.weather__icon{width:100px;height:100px}.weather__items{flex-wrap:wrap}.weather__item{font-size:25px}.weather__item span{font-size:50px}.weather__list li{font-size:20px;line-height:30px}}
