id-column-gap:5px;-webkit-column-gap:5px;column-gap:5px}.weather-daily__block::before{right:0}.weather-daily__img{width:45px;height:45px}.weather-daily__full-content{margin:0}.weather-daily .full-content__items{grid-template:1fr/150px repeat(4, 1fr)}}@media(max-width: 576px){.weather-daily .full-content__items{grid-template:1fr/120px repeat(4, 1fr)}}
