body {
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: url(/Weather/static/media/blue_sky.3311531d.jpg) 0 0/cover no-repeat fixed;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(26, 26, 26);
}

* {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

